import {
	mapState
} from 'vuex'
import {
	toMoney
} from '@/vendor/filters.js'
import {
	getTasks
} from '@/api/index.js'

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	data() {
		return {
			items: [],
			page: 0,
			pageSize: 20
		}
	},
	onLoad(){
		uni.startPullDownRefresh()
	},
	
	onPullDownRefresh() {
		this.getItems().then(()=>{
			uni.stopPullDownRefresh();
		}).catch(()=>{
			uni.stopPullDownRefresh();
		});
	},		
	methods: {
		async getItems() {
			this.$$.showLoading();
			try {
				const items = await getTasks(this.page, this.pageSize);
				this.items = items.data;
				this.page++;
			} catch (e) {
				console.error(e);
				this.$$.showErr();
			}
			this.$$.hideLoading();
		},
		openDetail(id) {
			uni.navigateTo({
				url: `/pages/detail/detail?id=${id}`
			});
		}
	},
	filters: {
		toMoney: toMoney
	},
}
